:root {
  --nav-item-color: #00acdc;
}

* {
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo, .logo_link {
  width: 40px;
  height: 40px;
  display: block;
}

.nav_item {
  align-items: center;
  height: 60px;
  list-style-type: none;
  display: flex;
}

.nav_item:nth-child(n+2) {
  margin-left: 20px;
}

.nav_list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav_link:hover {
  color: var(--nav-item-color);
}

.is-active {
  color: var(--nav-item-color);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--nav-item-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin-top: -2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

